const linearGradient = (svg, id, colors = []) => {
    const defs = svg.append("defs");
    const gradient = defs.append("linearGradient")
        .attr("id", id)
        .attr("x1", "0%")
        .attr("y1", "0%")
        .attr("x2", "0%")
        .attr("y2", "100%"); // 垂直渐变

    // 定义渐变的起点和终点颜色
    const len = colors.length - 1
    for (let i in colors) {
        const c = colors[i]
        const v = Math.floor(i / len * 10000) / 100
        gradient.append("stop")
            .attr("offset", `${v}%`)
            .attr("stop-color", c); // 起始颜色
    }

}

export {
    linearGradient
}